import React, { useState, useEffect } from 'react';
import { Box, Button, Typography, Dialog, DialogContent, DialogActions, CircularProgress } from '@mui/material';
import QuestionDisplay from '../QuestionDisplay';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

function ConnectFour({ onQuestionNeeded, onQuestionAnswered }) {
  const [board, setBoard] = useState(Array(6).fill().map(() => Array(7).fill(null)));
  const [isPlayerTurn, setIsPlayerTurn] = useState(true);
  const [winner, setWinner] = useState(null);
  const [showQuestion, setShowQuestion] = useState(false);
  const [questionsAnswered, setQuestionsAnswered] = useState([]);
  const [score, setScore] = useState(0);
  const [currentQuestion, setCurrentQuestion] = useState(null);
  const [showTryAgain, setShowTryAgain] = useState(false);
  const [lastMove, setLastMove] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [gameStatus, setGameStatus] = useState('playing'); // 'playing', 'question', 'aiTurn', 'gameOver'

  // Effect to handle AI move
  useEffect(() => {
    if (gameStatus === 'aiTurn' && !winner) {
      const timer = setTimeout(() => {
        makeAIMove();
      }, 1000);
      return () => clearTimeout(timer);
    }
  }, [gameStatus]);

  const checkWinner = (board) => {
    // Check horizontal
    for (let row = 0; row < 6; row++) {
      for (let col = 0; col < 4; col++) {
        if (board[row][col] && 
            board[row][col] === board[row][col + 1] && 
            board[row][col] === board[row][col + 2] && 
            board[row][col] === board[row][col + 3]) {
          return board[row][col];
        }
      }
    }

    // Check vertical
    for (let row = 0; row < 3; row++) {
      for (let col = 0; col < 7; col++) {
        if (board[row][col] && 
            board[row][col] === board[row + 1][col] && 
            board[row][col] === board[row + 2][col] && 
            board[row][col] === board[row + 3][col]) {
          return board[row][col];
        }
      }
    }

    // Check diagonal (down-right)
    for (let row = 0; row < 3; row++) {
      for (let col = 0; col < 4; col++) {
        if (board[row][col] && 
            board[row][col] === board[row + 1][col + 1] && 
            board[row][col] === board[row + 2][col + 2] && 
            board[row][col] === board[row + 3][col + 3]) {
          return board[row][col];
        }
      }
    }

    // Check diagonal (down-left)
    for (let row = 0; row < 3; row++) {
      for (let col = 3; col < 7; col++) {
        if (board[row][col] && 
            board[row][col] === board[row + 1][col - 1] && 
            board[row][col] === board[row + 2][col - 2] && 
            board[row][col] === board[row + 3][col - 3]) {
          return board[row][col];
        }
      }
    }

    return null;
  };

  const makeAIMove = () => {
    const availableColumns = [];
    for (let col = 0; col < 7; col++) {
      if (board[0][col] === null) {
        availableColumns.push(col);
      }
    }

    if (availableColumns.length > 0) {
      const randomCol = availableColumns[Math.floor(Math.random() * availableColumns.length)];
      const newBoard = [...board];
      for (let row = 5; row >= 0; row--) {
        if (newBoard[row][randomCol] === null) {
          newBoard[row][randomCol] = 'O';
          break;
        }
      }
      setBoard(newBoard);
      const newWinner = checkWinner(newBoard);
      if (newWinner) {
        setWinner(newWinner);
        setGameStatus('gameOver');
      } else if (newBoard.every(row => row.every(cell => cell !== null))) {
        setWinner('Draw');
        setGameStatus('gameOver');
      } else {
        setGameStatus('playing');
      }
    }
    setIsPlayerTurn(true);
  };

  const handleColumnClick = async (col) => {
    if (!isPlayerTurn || winner || board[0][col] !== null || gameStatus !== 'playing') return;

    // Place player's chip
    const newBoard = [...board];
    let rowIndex = -1;
    for (let row = 5; row >= 0; row--) {
      if (newBoard[row][col] === null) {
        newBoard[row][col] = 'X';
        rowIndex = row;
        break;
      }
    }
    setBoard(newBoard);
    setLastMove({ row: rowIndex, col });

    // Check for winner after player move
    const newWinner = checkWinner(newBoard);
    if (newWinner) {
      setWinner(newWinner);
      setScore(score + 1);
      setGameStatus('gameOver');
      return;
    }

    if (newBoard.every(row => row.every(cell => cell !== null))) {
      setWinner('Draw');
      setGameStatus('gameOver');
      return;
    }

    // Player's turn is over, now we need to show a question
    setIsPlayerTurn(false);
    setGameStatus('question');
    setIsLoading(true);
    
    try {
      console.log('Getting question...');
      const question = await onQuestionNeeded();
      console.log('Received question:', question);
      setIsLoading(false);
      
      if (question) {
        setCurrentQuestion(question);
        setShowQuestion(true);
      } else {
        console.log('No question received, reverting to player turn');
        setIsPlayerTurn(true);
        setGameStatus('playing');
      }
    } catch (error) {
      console.error('Error fetching question:', error);
      setIsLoading(false);
      setIsPlayerTurn(true);
      setGameStatus('playing');
    }
  };

  const handleQuestionAnswered = (isCorrect) => {
    console.log('Question answered:', isCorrect);
    setShowQuestion(false);
    setCurrentQuestion(null);
    
    if (isCorrect) {
      // If correct, record it and let AI make a move
      setQuestionsAnswered([...questionsAnswered, { correct: true }]);
      setScore(score + 1);
      setGameStatus('aiTurn');
      onQuestionAnswered(isCorrect);
    } else {
      // If incorrect, let the player try again
      setQuestionsAnswered([...questionsAnswered, { correct: false }]);
      setShowTryAgain(true);
      onQuestionAnswered(isCorrect);
    }
  };

  const handleTryAgain = () => {
    setShowTryAgain(false);
    if (lastMove) {
      // Remove the player's last move
      const newBoard = [...board];
      newBoard[lastMove.row][lastMove.col] = null;
      setBoard(newBoard);
      setLastMove(null);
    }
    setIsPlayerTurn(true);
    setGameStatus('playing');
  };

  const resetGame = () => {
    setBoard(Array(6).fill().map(() => Array(7).fill(null)));
    setIsPlayerTurn(true);
    setWinner(null);
    setQuestionsAnswered([]);
    setScore(0);
    setShowQuestion(false);
    setCurrentQuestion(null);
    setShowTryAgain(false);
    setLastMove(null);
    setGameStatus('playing');
  };

  return (
    <Box sx={{ textAlign: 'center' }}>
      <Typography variant="h5" gutterBottom>
        {winner ? 
          `Winner: ${winner === 'X' ? 'You' : winner === 'O' ? 'AI' : 'Draw'}` : 
          isPlayerTurn ? 
            'Your turn' : 
            gameStatus === 'aiTurn' ? 
              'AI is thinking...' : 
              'Answer the question to continue'
        }
      </Typography>
      <Typography variant="h6" gutterBottom>
        Score: {score}
      </Typography>
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        {board.map((row, rowIndex) => (
          <Box key={rowIndex} sx={{ display: 'flex' }}>
            {row.map((_, colIndex) => (
              <Button
                key={colIndex}
                variant="outlined"
                sx={{
                  width: '60px',
                  height: '60px',
                  minWidth: '60px',
                  margin: '2px',
                  backgroundColor: board[rowIndex][colIndex] === 'X' ? '#ff5252' : board[rowIndex][colIndex] === 'O' ? '#2196f3' : 'white',
                  '&:hover': {
                    backgroundColor: board[rowIndex][colIndex] === null ? '#e0e0e0' : undefined,
                  },
                }}
                onClick={() => handleColumnClick(colIndex)}
                disabled={!isPlayerTurn || winner || board[0][colIndex] !== null}
              />
            ))}
          </Box>
        ))}
      </Box>

      {isLoading && (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
          <CircularProgress />
        </Box>
      )}

      <Dialog 
        open={showQuestion} 
        onClose={() => {}}
        disableEscapeKeyDown
        sx={{
          '& .MuiDialog-paper': {
            width: '80%',
            maxWidth: '500px'
          }
        }}
      >
        <DialogContent>
          {currentQuestion && (
            <QuestionDisplay
              question={currentQuestion.question}
              options={currentQuestion.options}
              correctAnswer={currentQuestion.correctAnswer}
              onAnswer={handleQuestionAnswered}
            />
          )}
        </DialogContent>
      </Dialog>

      <Dialog 
        open={showTryAgain} 
        onClose={() => {}}
        disableEscapeKeyDown
      >
        <DialogContent>
          <Typography variant="h6" gutterBottom>
            Incorrect Answer!
          </Typography>
          <Typography>
            Try again to place your piece.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleTryAgain} color="primary" variant="contained">
            Try Again
          </Button>
        </DialogActions>
      </Dialog>

      {winner && (
        <Button
          variant="contained"
          color="primary"
          onClick={resetGame}
          sx={{ mt: 2 }}
        >
          Play Again
        </Button>
      )}
    </Box>
  );
}

export default ConnectFour; 