import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { createTheme, ThemeProvider, CssBaseline } from '@mui/material';
import GameManager from './components/GameManager';
import Navbar from './components/Navbar';

const theme = createTheme({
  palette: {
    primary: {
      main: '#6366f1',
    },
    secondary: {
      main: '#ec4899',
    },
    background: {
      default: '#f9fafb',
    },
  },
  typography: {
    fontFamily: "'Inter', 'Roboto', 'Helvetica', 'Arial', sans-serif",
  },
  shape: {
    borderRadius: 8,
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          fontWeight: 600,
        },
      },
    },
  },
});

// Add CSS variables for gradient colors
document.documentElement.style.setProperty('--primary-color', theme.palette.primary.main);
document.documentElement.style.setProperty('--secondary-color', theme.palette.secondary.main);

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Navbar />
        <Routes>
          <Route path="/" element={<GameManager />} />
          <Route path="/tic-tac-toe" element={<GameManager />} />
          <Route path="/connect-four" element={<GameManager />} />
          <Route path="/geometry-runner" element={<GameManager />} />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App; 